.login-page {
    background-color: rgb(230, 231, 226);
    height: 100vh;
    width: 100vw;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    flex-direction: column;
  }

  .logo {
    width: 30%;
  }
  
  .login-btn {
    width: 200px;
    padding: 15px 0px;
    text-align: center;
    background-color: #fefefe;
    border-radius: 50px;
    color: #1f1f1f;
    font-weight: 600;
    margin-top: -35%;
  }
  
  a {
    text-decoration: none;
  }


  @media (max-width: 768px) {
    .login-btn {
      width: 100px;
      padding: 8px 0px;
      text-align: center;
      background-color: #fefefe;
      border-radius: 50px;
      color: #1f1f1f;
      font-weight: 600;
      margin-top: -10%;
    }
    }
  