.body {
    padding: 30px;
    width: 100%;
    height: 100vh;
    overflow-y: overlay;
    flex: 0.8;
    color: white;
    background: linear-gradient(transparent, rgba(0, 0, 0, 1));
    background-color: rgb(91, 87, 115);
  }
  
  .body::-webkit-scrollbar {
    display: none;
  }
  
  .body__info {
    display: flex;
    align-items: flex-end;
    padding: 10px;
    

    
  }
  .block-align{
    display: flex;
    
    height: auto;
    flex-wrap: wrap;
  }

  .block-align::-webkit-scrollbar {
    display: none;
  }